import React from "react"
import { Link } from "gatsby"
import { Col, Image } from "react-bootstrap"

const Blog = (props) => {
    let date = new Date(props.date).toLocaleDateString("fa-IR")
    let url = props.slug.split("/");
    url.splice(0 , 1)
    let image = props.image;
    return (
         <Col  md={6} className="post__box mt-5" key={props.key}>
                                <Link to={url[0]}>
                                    <div>
                                        {<Image  width={347} height={192} src={"image/" +props.image} />}
                                    </div>                    
                                </Link>
                                <span className="post__box--date">{date}</span>
                                <Link to={url[0]}>
                                    <h4>{props.title}</h4>
                                </Link>
        </Col>
    )
}

export default Blog
